import * as React from 'react';
import { AppBar, Toolbar, Typography, IconButton, Switch, FormControlLabel, Stack, useMediaQuery } from '@mui/material';
import DarkModeIcon from '@mui/icons-material/DarkMode';
import LightModeIcon from '@mui/icons-material/LightMode';
import { useTheme } from '@mui/material/styles';
import { useLanguage } from '../context/LanguageContext';
import { Button } from '@mui/material';


const Navbar = ({ toggleTheme, isDarkMode, onSale, onSaleToggle }) => {
  const themeIcon = isDarkMode ? <LightModeIcon /> : <DarkModeIcon />;
  const { language, toggleLanguage, textDirection } = useLanguage();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const logoText = {
    en: 'ShikSearch',
    fa: 'شیک سرچ'
  };

  const onSaleLabel = {
    en: 'On Sale',
    fa: 'تخفیف دار',
  };

  return (
    <AppBar position="static" color="default" sx={{ direction: textDirection }}>
      <Toolbar sx={{ display: 'flex', justifyContent: 'space-between', backgroundColor: theme.palette.background.paper }}>
        <Button onClick={toggleLanguage} variant="outlined" color="inherit">
          {language === 'en' ? 'FA' : 'EN'}
        </Button>

        <Typography variant="h6" component="div" sx={{ flexGrow: 1, textAlign: 'center' }}>
          {logoText[language]}
        </Typography>

        <Stack direction={isMobile ? 'row' : 'row'} alignItems="center" spacing={1}>          
          {/* Mobile switch with label below */}
          {isMobile && (
            <Stack direction="column" alignItems="center" spacing={0.5}>
              <Switch
                checked={onSale}
                onChange={onSaleToggle}
                size="small"
              />
              <Typography variant="caption">{onSaleLabel[language]}</Typography>
            </Stack>
          )}

          {/* Non-mobile switch with label on side */}
          {!isMobile && (
            <Switch
              checked={onSale}
              onChange={onSaleToggle}
            />
          )}
          {!isMobile && (
            <Typography variant="body1" noWrap>
              {onSaleLabel[language]}
            </Typography>
          )}
           <IconButton
            size="large"
            color="inherit"
            aria-label="toggle theme"
            onClick={toggleTheme}
          >
            {themeIcon}
          </IconButton>
        </Stack>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;